.business_card {
  width: 300px;
  height: 150px;
  padding: 5px;
  margin: 10% auto 0;
  position: relative;
  box-shadow: 2px 2px 8px #ddd;
  background-color: #eeeeee36;
  border-right: 20px solid #5b84c1;
  border-radius: 5px; }
  .business_card > div {
    position: absolute; }
  .business_card .card_name {
    top: 20px;
    right: 16px;
    font-size: 2rem;
    font-weight: bold; }
  .business_card .card_birth_day {
    top: 50px;
    right: 20px;
    color: #5b84c1; }
  .business_card .card_school {
    top: 70px;
    right: 20px;
    font-size: 0.8rem;
    font-weight: bold;
    border-bottom: 1px solid #5b84c1; }
  .business_card .card_text {
    top: 100px;
    right: 20px;
    font-size: 0.8rem;
    text-align: left; }
  .business_card .card_logo_img {
    top: 0px;
    left: 10px; }
    .business_card .card_logo_img img {
      width: 38px; }

.history_box {
  position: relative;
  width: 300px;
  margin: 0 auto;
  padding-bottom: 5px; }
  .history_box .history_date {
    font-size: 1.5rem;
    padding: 30px;
    border: 5px solid #5b84c1;
    border-radius: 50px;
    text-align: center;
    color: #8c62b8;
    font-weight: bold; }
  .history_box .history_info {
    margin-top: 5px;
    margin-left: 50%;
    padding-left: 5%;
    border-left: 5px solid #5b84c1; }
    .history_box .history_info > div {
      margin: 10px 0; }
      .history_box .history_info > div span {
        font-size: 0.8rem;
        font-weight: bold;
        color: #5b84c1; }

