.pop_preview {
  height: calc(50vh - 38px); }
  .pop_preview > div {
    float: left; }
  .pop_preview::after {
    clear: both;
    display: block;
    content: ''; }
  .pop_preview .menu-list {
    width: 100px;
    height: calc(50vh - 38px);
    padding-right: 4px;
    border-right: 1px solid #ddd; }
    .pop_preview .menu-list li {
      font-size: 0.8rem;
      cursor: pointer;
      padding: 5px 0 5px 15px;
      background: url(data:image/png;base64,) no-repeat 2px 8px;
      background-size: 10%; }
      .pop_preview .menu-list li:hover {
        background-color: #e0f5f9; }
  .pop_preview .sub-content {
    width: calc(100% - 110px);
    height: calc(50vh - 38px);
    margin-left: 5px;
    white-space: nowrap;
    overflow-x: auto; }

.menu-list {
  height: calc(100vh - 38px); }

.sub-content {
  height: calc(100vh - 38px); }
