div.max_modal #map {
  zoom: 1.8; }

div.max_modal .mobile-joystick {
  left: 3%;
  bottom: 15% !important; }

.gameboy {
  background-color: #000;
  width: 100%;
  height: 100%;
  outline: none; }
  .gameboy #map {
    background: url("https://i.imgur.com/cR9pz0W.jpg") no-repeat;
    width: 384px;
    height: 320px;
    zoom: 1;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    position: relative; }
    .gameboy #map::after {
      content: '';
      clear: both;
      display: block; }
  .gameboy .land {
    float: left;
    width: 16px;
    height: 16px; }
  .gameboy .L2 {
    background: url("https://i.imgur.com/gmbmP6M.png");
    background-position: 0 0;
    background-repeat: no-repeat; }
    .gameboy .L2.left-move {
      background-position: -32px 0; }
    .gameboy .L2.up-move {
      background-position: -16px 0; }
    .gameboy .L2.right-move {
      background-position: -48px 0; }
    .gameboy .L2.down-right {
      background-position: 0 0; }
  .gameboy .L1 {
    background: rgba(0, 255, 0, 0); }
  .gameboy .L0 {
    background: rgba(255, 0, 0, 0); }

.mobile-joystick {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 5000;
  zoom: 0.8; }
  .mobile-joystick .mobile-controller {
    position: relative;
    width: 200px;
    height: 20vh;
    background-color: #fff;
    opacity: 0.3;
    border-radius: 20%; }
    .mobile-joystick .mobile-controller div {
      position: absolute; }
      .mobile-joystick .mobile-controller div button {
        padding: 20px;
        border: 5px solid #000;
        border-radius: 7em 0 0 7em;
        outline: 0; }
      .mobile-joystick .mobile-controller div:nth-child(1) {
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
        top: 0;
        left: 40%; }
        .mobile-joystick .mobile-controller div:nth-child(1) button {
          background: ddd; }
      .mobile-joystick .mobile-controller div:nth-child(2) {
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg);
        top: 35%;
        right: 0; }
      .mobile-joystick .mobile-controller div:nth-child(3) {
        -webkit-transform: rotate(270deg);
                transform: rotate(270deg);
        left: 40%;
        bottom: 0; }
      .mobile-joystick .mobile-controller div:nth-child(4) {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
        top: 35%; }

.mobile-joyBtns {
  position: fixed;
  bottom: 0;
  right: 20%;
  z-index: 500; }
  .mobile-joyBtns .mobile-joy-btn div {
    float: left; }
    .mobile-joyBtns .mobile-joy-btn div::after {
      content: '';
      display: block;
      clear: both; }

@media screen and (max-width: 1150px) {
  div.max_modal #map {
    zoom: 1.5 !important; }
  #map {
    zoom: 1 !important; } }

@media screen and (max-width: 900px) {
  div.max_modal #map {
    zoom: 1 !important; }
  #map {
    zoom: 0.7; } }

@media screen and (max-width: 550px) {
  div.max_modal #map {
    zoom: 1 !important; }
  #map {
    zoom: 0.7 !important; } }
